@import '../../../../Global/ScssPartials/utilities';

.MenuOptions {
  @include grid-cell();

  @supports (-moz-appearance: none) {
    /* Firefox-specific styles */
    margin-block: 2.5em;
  }

  @supports not (-moz-appearance: none) {
    /* Non-Firefox styles */
    margin-top: -1.5em;
  }
}
